.OrderItem {
	display: grid;
	grid-template-columns: auto 1fr auto auto;
	gap: 16px;
	margin-block: 16px;
	align-items: center;
}

.OrderItem figure {
	margin: 0;
}

.OrderItem figure img {
	width: 70px;
	height: 70px;
	border-radius: 20px;
	object-fit: cover;
}

.OrderItem p:nth-child(2) {
	color: var(--gray-color);
    font-size: 14px;
    font-weight: 600;
}

.OrderItem p:nth-child(3) {
	font-size: 14px;
	font-weight: bold;
}